import React, { useContext, useRef } from 'react'
import { ReactComponent as Icon } from "../assits/search-icon.svg"
import { timeContext } from './App'

export default function Search( {  changeCity } ) {
  // use useContext
  const cityInput = useRef()
  const time = useContext(timeContext)
  
  const styleInput = {
    background: time.hour < 12 ?  "linear-gradient( 20deg, #005f8f , #003049 )" : "linear-gradient( 20deg, #fbbd48, #d62828)"
  }
  const styleIcon = {
    background: time.hour < 12 ?  "linear-gradient( 220deg, #005786 , #003049 100% )" : "linear-gradient( -135deg, #fbbd48 55%, #d62828  )"
  }
  
  return (
    <div className="search-container">
      <input ref={cityInput} onChange={changeCity} onKeyPress={e => { if(e.key === "Enter") { 
        changeCity(e.target.value)
        e.target.value = ""  
      }}} type="text" style={styleInput}/>
      <Icon className="icon" style={styleIcon} onClick={() => {
        changeCity(cityInput.current.value)
        cityInput.current.value = ""
      }}/>
    </div>
  )
}
