import React, { useContext } from 'react'
import { timeContext, weatherOb } from './App'




export default function WeatherCard() {
  const weather = useContext(weatherOb)
  
  
  function IfRespond() {
    const data = weather.list[0]
    console.log(data);

    return (
      <>
      <div className='col-1'>
        <h1>{data.name} ,{data.sys.country}</h1>
        <span></span>
        <h2>درجة الحرارة</h2>
        <h1 className='temp'>{Math.round(data.main.temp)} </h1>
        <h2>حالة الطقس</h2>
        <h3>{data.weather[0].description}</h3>
      </div>
      <div className='col-2'>
        <img src={`http://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`} alt="bruh" />
        
        </div>
      </>
    )
  }
  function IfNotRespond() {


    return (
      <h1 className='error'>مالقيت شي</h1>
    )
  }
  const time = useContext(timeContext)
  
  
  const bgColor = {
    background: time.hour < 12 ? "linear-gradient(45deg,#005786 40%, #131f40 )" : "linear-gradient(45deg, #fcbe49, #d62828 )" 
  }
  return (
    <div className='card-container' style={bgColor}>
      <div className='card'>
        {weather.list && weather.list[0] ? <IfRespond /> : <IfNotRespond />}
      </div>
    </div>
  )
}
